<template lang="pug">
  div.messages 
    hd-bar.messages-hd-bar( title-name="消息" )
      div( slot="sec-r" )
        span.iconfont.icon-tianjiajia
    div.messages-wrap
      div.messages-main.wrapper(ref="bscroll") 
        div.messages-bscroll 
          div.find-friend 
            button.find-btn 搜索
          ul.message-list 
            li.message-item( v-for=" friend in friendList ", @click="$router.push({name: 'chat', params: { id: friend.friend._id}})") 
              div.headimg
                img( :src="userInfo.avatar")
              div.message-info
                .message-top.clearfix
                  div.message-fromer.fl {{friend.friend.nickname}}
                  div.message-time.fr 下午 11.00
                .message-bottom    
                  p.message-content.text_ellipsis.fl 电视剧发神经的发掘的撒撒娇；的发髻上的发动机发京东方
                  dot.fr
    bt-tab              
</template>
<script>
import hdBar from '@/components/hd-bar'
import btTab from '@/components/bt-tab'
import dot from '@/components/dot'
import { socketEmit } from '@/socket/socket'
import { mapState } from 'vuex'
export default {
  components: {
    hdBar,
    dot,
    btTab
  },
  data() {
    return {
      friendList: []
    }
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  mounted() {
    // let scroll = new BScroll(this.$refs['bscroll'],{
    //   scrollY: true,
    //     click: true,
    //     bindToWrapper: true
        
    // })
  },
  created() {

    this.onLine();
    this.getFriendList();
  },
  methods: {
    getFriendList() {
      socketEmit('getFriendList').then(res => {
        console.log(res.data, 'getFriendList')
        this.friendList = res.data;
      })
    },
    onLine() {
      socketEmit('online').then(data => {
        console.log(data, 'online')
      })
    },
    getUserInfo() {
      let token = localStorage.getItem('_token');
      socketEmit('getUserInfo', { token }).then(data => {
        console.log(data, '')
      }).catch(err => {
        console.log(err, 'err')
      });
    }
  }
}
</script>

<style lang="stylus" scoped>

.messages-wrap 
  padding-top $hd-bar-height
.messages-main
  background #323232
  height 100vh
  .messages-bscroll 
    background #ffffff
    height 101vh
  .find-friend 
    padding .5rem .65rem 
    .find-btn  
      border none
      width 100% 
      text-align center 
      padding .4rem 0
      border-radius 4px 
      background-color #f3f3f3 
      color #c9c8cd
      outline none
  .message-list  
    .message-item 
      align-items center
      padding 0.65rem .65rem
      border-bottom 1px solid #e1e1e1
      display flex
      box-sizing border-box
      .headimg 
        height 3.05rem 
        width 3.05rem 
        border-radius 50% 
        img 
          border-radius 50%  
          width 100% 
          height 100% 
      .message-info
         flex 1
         margin-left .5rem 
         margin-right 0.3rem
        .message-top 
          margin-bottom .4rem  
          .message-fromer 
            font-size 16px
          .message-time 
            color #7f7f7f 
        .message-bottom 
          .message-content 
            width 14.5rem
            color #7f7f7f
       
             


</style>



