<template lang="pug">
  .dot 
    span {{num}}
</template>
<script>
export default {
  props: {
    num: {
      type: Number,
      default: 1
    }
  }
}
</script>
<style lang="stylus" scoped>
  .dot 
    display inline-block
    height 1.05rem 
    line-height 1.05rem 
    width auto 
    padding 0 .4rem 
    background-color #fc4932 
    border-radius 120px
    span
      font-size 1rem
      color #ffffff

</style>





